import type { Middleware, Dispatch } from "redux";
import {
  WS_CONNECTION_INIT,
  WS_SEND_MESSAGE,
  WS_CONNECTION_CLOSE,
} from "../actions/ws-actions";
import { TActions } from "../../utils/tactions";
import { TFeedWsResponse } from "../../utils/types";

export const socketMiddleware: () => Middleware<
  {},
  {},
  Dispatch<TActions>
> = () => {
  return (store) => {
    const connections: {
      [id: string]: {
        socket: WebSocket;
        wsUrl: string;
        id: string;
        actions: {
          start: string;
          open: string;
          error: string;
          message: string;
          close: string;
        };
      };
    } = {};
    return (next: Dispatch<TActions>) => (action: TActions) => {
      const { dispatch } = store;
      const { type } = action;

      if (type === WS_CONNECTION_INIT) {
        const { wsUrl, id, actions } = action.payload;

        if (connections[id]) {
          return next(action);
        }

        //   WebSocket
        const socket = new WebSocket(wsUrl);
        connections[id] = { socket, id, wsUrl, actions };
        console.log("wsMiddleware", "socket created", {
          socket,
          id,
          wsUrl,
          actions,
        });

        dispatch({ type: actions.start });

        // ,     
        socket.onopen = (event) => {
          console.log("wsMiddleware", "socket open event", { id, event });
          dispatch({
            type: actions.open,
          });
        };

        // ,     
        socket.onerror = (event) => {
          console.log("wsMiddleware", "socket error event", id, event);
          dispatch({
            type: actions.error,
          });
          if (connections[id]) {
            connections[id].socket.close();
            delete connections[id];
            console.log("wsMiddleware", "connection deleted", { id });
          }
        };

        // ,       
        socket.onmessage = (event: MessageEvent<string>) => {
          const data = JSON.parse(event.data) as TFeedWsResponse;
          console.log("wsMiddleware", "message event", { data });
          if (!data.success) {
            dispatch({
              type: actions.error,
            });
            if (connections[id]) {
              connections[id].socket.close();
              delete connections[id];
              console.log("wsMiddleware", "connection deleted", { id });
            }
          } else {
            dispatch({
              type: actions.message,
              payload: { data },
            });
          }
        };

        // ,     
        socket.onclose = (event) => {
          console.log("wsMiddleware", "close event", { id, event });
          dispatch({
            type: actions.close,
          });
          if (connections[id]) {
            delete connections[id];
            console.log("wsMiddleware", "connection deleted", { id });
          }
        };
      }

      if (type === WS_SEND_MESSAGE) {
        const { message, id } = action.payload;
        const connection = connections[id];
        if (connection) {
          //      
          connection.socket.send(JSON.stringify(message));
        }
      }

      if (type === WS_CONNECTION_CLOSE) {
        const { id } = action.payload;
        const connection = connections[id];
        if (connection) {
          connection.socket.close();
          delete connections[id];
        }
      }

      next(action);
    };
  };
};
